<!-- eslint-disable -->
<template>
  <div class="historia-wrapper">
    <div class="historia movement">
      <div class="copy">
        <h2 class="title">A nossa hist√≥ria</h2>
        <div class="container">
          <div class="p">
            <div class="paragraph" v-html="text1"></div>
            <div class="line"></div>
          </div>
          <div class="paragraph" v-html="text2"></div>
          <!-- <div class="quote" v-html="quote"></div> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      text1: "",
      text2: "",
      quote: "",
    };
  },
  methods: {
    FadeIn() {
      var allMods = $(".movement, .cover");
      var delayedMods = $(".movement-delayed");
      var lateralMods = $(".lateral, .lateralR");
      var svg = $("#svg");

      (function ($) {
        $.fn.visible = function (partial, adjust) {
          if (adjust === undefined) {
            adjust = 0;
          }
          var elem = $(this),
            wind = $(window),
            viewTop = wind.scrollTop(),
            viewBottom = viewTop + wind.height(),
            elem_top = elem.offset().top + adjust,
            elem_bottom = elem_top + elem.height() - adjust,
            compareTop = partial === true ? elem_bottom : elem_top,
            compareBottom = partial === true ? elem_top : elem_bottom;

          return compareBottom <= viewBottom && compareTop >= viewTop;
        };
      })(jQuery);

      var win = $(window);

      var xmlTeste = true;

      allMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          el.addClass("placed");
        }
      });
      svg.each(function (i, el) {
        var el = $(el);
        if (el.visible(true)) {
          xmlTeste &&
            el.append(
              "<img src='assets/img/elemento_animated.svg' alt=''></img>"
            );
          xmlTeste = false;
        }
      });
      lateralMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 100)) {
          el.addClass("placed");
        }
      });

      delayedMods.each(function (i, el) {
        var el = $(el);
        if (el.visible(true, 800)) {
          el.addClass("placed");
        }
      });

      win.scroll(function (event) {
        allMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            el.addClass("placed");
          }
        });
        lateralMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 100)) {
            el.addClass("placed");
          }
        });
        delayedMods.each(function (i, el) {
          var el = $(el);
          if (el.visible(true, 800)) {
            el.addClass("placed");
          }
        });
        svg.each(function (i, el) {
          var el = $(el);
          if (el.visible(true)) {
            xmlTeste &&
              el.append(
                "<img src='assets/img/elemento_animated.svg' alt=''></img>"
              );
            xmlTeste = false;
          }
        });
      });
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getAbout.php")
      .then((resp) => {
        const data = resp.data;
        var result = data.split("||");
        var intro = JSON.parse(result[1]);

        vm.text1 = intro[0].text;
        vm.text2 = intro[0].text2;
        vm.quote = intro[0].quote;
      })
      .then(() => {
        vm.FadeIn();
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.historia-wrapper {
  background-color: $white;
  height: pxToEm(510);
  padding: 0 pxToEm(300);
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    height: auto;
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    height: auto;
    padding: 0 pxToEm(20);
  }

  .historia {
    margin: auto;
    max-width: pxToEm(1500);
    width: 100%;

    @media only screen and (min-width: 768px) and (max-width: 991px) {
      margin: pxToEm(40) 0;
    }

    @media only screen and (min-width: 600px) and (max-width: 767px) {
      margin: pxToEm(20) 0;
    }

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      margin: pxToEm(20) 0;
    }

    .copy {
      display: flex;
      flex-wrap: wrap;
      height: pxToEm(253);
      flex-direction: column;

      @media only screen and (max-width: 991px) {
        flex-wrap: nowrap;
        height: auto;
        align-items: center;
        text-align: center;
      }

      .title {
        margin: 0;
        font-weight: normal;
        font-size: pxToEm(40);
        color: $turquiose;
        margin-bottom: pxToEm(30);
      }

      .container {
        display: flex;
        justify-content: space-between;

        @media only screen and (max-width: 767px) {
          flex-direction: column;
        }

        .p {
          display: flex;
          flex-direction: column;
          // max-width: pxToEm(730);
          width: 49%;

          @media only screen and (max-width: 767px) {
            width: 100%;
          }

          .paragraph {
            width: 100% !important;
            text-align: justify;
          }
        }

        .paragraph {
          font-family: "OS-Regular";
          font-size: pxToEm(22);
          color: #828282;
          max-width: pxToEm(730);
          width: 100%;
          height: 100%;
          text-align: justify;

          @media only screen and (min-width: 768px) and (max-width: 1199px) {
            // margin: 0 pxToEm(10);
            // max-width: pxToEm(650);
            width: 49%;
            max-width: unset;
          }

          @media only screen and (max-width: 767px) {
            margin-bottom: pxToEm(20);
            text-align: left;
            // width: 49%;
            width: 100%;
            max-width: unset;
          }
        }

        .line {
          height: pxToEm(2);
          width: pxToEm(690);
          background-color: $turquiose;
          margin-top: pxToEm(15);

          @media only screen and (min-width: 768px) and (max-width: 1199px) {
            width: 100%;
          }

          @media only screen and (max-width: 767px) {
            margin-bottom: pxToEm(20);
            width: 100%;
          }
        }

        .quote {
          font-size: pxToEm(20);
          font-family: Georgia, "Times New Roman", Times, serif;
          color: #828282;
          max-width: pxToEm(600);
          width: 100%;
          margin-top: pxToEm(15);

          @media only screen and (max-width: 991px) {
            margin-bottom: pxToEm(20);
          }
        }
      }
    }
  }
}
</style>



